"use client";
import {
  ChevronLeft,
  ChevronRight,
  Edit,
  MoreHorizontal,
  Trash2,
  Building2,
  MapPin,
  CalendarDays,
  Briefcase,
  AlertCircle,
  ChevronFirst,
  ChevronLast,
} from "lucide-react";
import { Avatar, AvatarFallback, AvatarImage } from "./ui/avatar";
import { Popover, PopoverContent, PopoverTrigger } from "./ui/popover";
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "./ui/table";
import { useSelector, useDispatch } from "react-redux";
import { useRouter } from "next/navigation";
import moment from "moment";
import axios from "axios";
import { COMPANY_API_ENDPOINT } from "@/lib/constant";
import { toast } from "sonner";
import { deleteCompany } from "@/redux/slices/companySlice";
import { useEffect, useState } from "react";
import { Button } from "./ui/button";
import { Badge } from "./ui/badge";

const CompaniesTable = ({ companies = [] }) => {
  console.log("ALL Companies", companies);
  const [currentPage, setCurrentPage] = useState(1);
  const [isDeleting, setIsDeleting] = useState(false);
  const itemsPerPage = 5;

  const { searchCompanyByText } = useSelector((state) => state.company);
  const [filteredCompanies, setFilteredCompanies] = useState([]);
  const router = useRouter();
  const dispatch = useDispatch();
  const totalPages = Math.ceil(companies.length / itemsPerPage) || 1;
  const pageNumbers = Array.from({ length: totalPages }, (_, i) => i + 1);

  const handleDeleteCompany = async (companyId) => {
    setIsDeleting(true);
    try {
      const res = await axios.delete(
        `${COMPANY_API_ENDPOINT}/delete/${companyId}`,
        { withCredentials: true }
      );

      if (res.data.success) {
        toast.success(res.data.message, {
          icon: <CheckCircle2 className="w-4 h-4 text-green-500" />,
        });
        dispatch(deleteCompany(companyId));
      } else {
        toast.error(res.data.message, {
          icon: <AlertCircle className="w-4 h-4 text-red-500" />,
        });
      }
    } catch (error) {
      console.error(error);
      toast.error("An error occurred while deleting the company.", {
        icon: <AlertCircle className="w-4 h-4 text-red-500" />,
      });
    } finally {
      setIsDeleting(false);
    }
  };

  useEffect(() => {
    const filtered = companies.filter((company) => {
      const searchTerm = searchCompanyByText?.toLowerCase() || "";
      const nameMatch = company?.name?.toLowerCase().includes(searchTerm);
      const locationMatch = company?.location
        ?.toLowerCase()
        .includes(searchTerm);
      return nameMatch || locationMatch;
    });

    const startIndex = (currentPage - 1) * itemsPerPage;
    const endIndex = startIndex + itemsPerPage;
    const paginatedCompanies = filtered.slice(startIndex, endIndex);

    setFilteredCompanies(paginatedCompanies);
  }, [companies, searchCompanyByText, currentPage]);

  const getInitials = (name) => {
    return (
      name
        ?.split(" ")
        .map((n) => n[0])
        .join("")
        .toUpperCase() || "CO"
    );
  };

  return (
    <div className="border rounded-lg overflow-hidden shadow-sm dark:border-gray-700">
      <Table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
        <TableHeader className="bg-gray-50 dark:bg-gray-800">
          <TableRow>
            <TableHead className="px-4 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
              <div className="flex items-center gap-2">
                <Building2 className="h-4 w-4" />
                Company
              </div>
            </TableHead>
            <TableHead className="px-4 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
              Location
            </TableHead>
            <TableHead className="px-4 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
              <div className="flex items-center gap-2">
                <Briefcase className="h-4 w-4" />
                Jobs Posted
              </div>
            </TableHead>
            <TableHead className="px-4 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
              <div className="flex items-center gap-2">
                <CalendarDays className="h-4 w-4" />
                Created
              </div>
            </TableHead>
            <TableHead className="px-4 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-400 uppercase tracking-wider">
              Actions
            </TableHead>
          </TableRow>
        </TableHeader>

        <TableBody className="bg-white dark:bg-gray-800 divide-y divide-gray-200 dark:divide-gray-700">
          {filteredCompanies.length === 0 ? (
            <TableRow>
              <TableCell colSpan={5} className="px-4 py-6 text-center">
                <div className="flex flex-col items-center justify-center py-4">
                  <AlertCircle className="h-10 w-10 text-gray-400 mb-3" />
                  <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                    No companies found
                  </h3>
                  <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                    {searchCompanyByText
                      ? "Try a different search term"
                      : "No companies have been registered yet"}
                  </p>
                </div>
              </TableCell>
            </TableRow>
          ) : (
            filteredCompanies.map((company) => (
              <TableRow
                key={company?._id}
                className="hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors"
                onClick={() =>
                  router.push(`/recruiter/companies/${company?._id}`)
                }
              >
                <TableCell className="px-4 py-4">
                  <div className="flex items-center gap-3">
                    <Avatar className="h-10 w-10">
                      {company?.logo ? (
                        <AvatarImage src={company.logo} alt={company.name} />
                      ) : (
                        <AvatarFallback className="bg-gray-100 dark:bg-gray-600 text-gray-800 dark:text-white">
                          {getInitials(company?.name)}
                        </AvatarFallback>
                      )}
                    </Avatar>
                    <div>
                      <div className="font-medium text-gray-900 dark:text-white">
                        {company?.name}
                      </div>
                      <div className="text-xs text-gray-500 dark:text-gray-400">
                        {company?.email || "No email"}
                      </div>
                    </div>
                  </div>
                </TableCell>

                <TableCell className="px-4 py-4">
                  <div className="flex items-center gap-2">
                    <MapPin className="h-4 w-4 text-gray-400" />
                    <span className="text-gray-900 dark:text-white">
                      {company?.location || "Not specified"}
                    </span>
                  </div>
                </TableCell>

                <TableCell className="px-4 py-4">
                  <Badge variant="outline" className="px-3 py-1">
                    {company?.jobs?.length || 0} jobs
                  </Badge>
                </TableCell>

                <TableCell className="px-4 py-4">
                  <div className="text-sm text-gray-900 dark:text-white">
                    {moment(company.createdAt).format("MMM D, YYYY")}
                  </div>
                  <div className="text-xs text-gray-500 dark:text-gray-400">
                    {moment(company.createdAt).fromNow()}
                  </div>
                </TableCell>

                <TableCell className="px-4 py-4 text-right">
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant="ghost"
                        size="icon"
                        className="h-8 w-8"
                        onClick={(e) => e.stopPropagation()}
                      >
                        <MoreHorizontal className="h-4 w-4" />
                        <span className="sr-only">Actions</span>
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-48 p-2" align="end">
                      <div className="space-y-1">
                        <Button
                          variant="ghost"
                          className="w-full justify-start"
                          onClick={(e) => {
                            e.stopPropagation();
                            router.push(
                              `/recruiter/companies/${company?._id}/edit`
                            );
                          }}
                        >
                          <Edit className="mr-2 h-4 w-4" />
                          Edit
                        </Button>
                        <Button
                          variant="ghost"
                          className="w-full justify-start text-red-600 hover:text-red-700 dark:text-red-500 dark:hover:text-red-600"
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDeleteCompany(company?._id);
                          }}
                          disabled={isDeleting}
                        >
                          <Trash2 className="mr-2 h-4 w-4" />
                          {isDeleting ? "Deleting..." : "Delete"}
                        </Button>
                      </div>
                    </PopoverContent>
                  </Popover>
                </TableCell>
              </TableRow>
            ))
          )}
        </TableBody>
      </Table>

      {/* Enhanced Pagination */}
      {totalPages > 1 && (
        <div className="bg-white dark:bg-gray-800 px-4 py-3 flex items-center justify-between border-t border-gray-200 dark:border-gray-700 sm:px-6">
          <div className="flex-1 flex items-center justify-between">
            <div className="hidden sm:block">
              <p className="text-sm text-gray-700 dark:text-gray-300">
                Showing{" "}
                <span className="font-medium">
                  {(currentPage - 1) * itemsPerPage + 1}
                </span>{" "}
                to{" "}
                <span className="font-medium">
                  {Math.min(currentPage * itemsPerPage, companies.length)}
                </span>{" "}
                of <span className="font-medium">{companies.length}</span>{" "}
                companies
              </p>
            </div>
            <div className="flex-1 flex items-center justify-center sm:justify-end gap-1">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(1)}
                disabled={currentPage === 1}
                className="hidden sm:inline-flex"
              >
                <ChevronFirst className="h-4 w-4" />
                <span className="sr-only">First</span>
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
                disabled={currentPage === 1}
              >
                <ChevronLeft className="h-4 w-4" />
                <span className="sr-only">Previous</span>
              </Button>

              <div className="flex items-center gap-1">
                {pageNumbers.map((page) => (
                  <Button
                    key={page}
                    variant={currentPage === page ? "default" : "outline"}
                    size="sm"
                    onClick={() => setCurrentPage(page)}
                    className={
                      page === 1
                        ? "rounded-r-none"
                        : page === totalPages
                        ? "rounded-l-none"
                        : ""
                    }
                  >
                    {page}
                  </Button>
                ))}
              </div>

              <Button
                variant="outline"
                size="sm"
                onClick={() =>
                  setCurrentPage((prev) => Math.min(prev + 1, totalPages))
                }
                disabled={currentPage === totalPages}
              >
                <ChevronRight className="h-4 w-4" />
                <span className="sr-only">Next</span>
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(totalPages)}
                disabled={currentPage === totalPages}
                className="hidden sm:inline-flex"
              >
                <ChevronLast className="h-4 w-4" />
                <span className="sr-only">Last</span>
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default CompaniesTable;
